<template>
  <main id="registration">
    <section class="authorization">
      <div class="authorization__wrapper">
        <h1 class="authorization__title">Регистрация</h1>
        <form class="authorization__form form" @submit.prevent="RegistrationUser">
        <div class="form__box">
            <label class="form__label" for="username"
              >Username</label
            >
            <input
              class="form__input"
              v-model="username"
              name="username"
              id="username"
              type="text"
              required
            />
          </div>
          <div class="form__box">
            <label class="form__label" for="email"
              >Адрес электронной почты</label
            >
            <input
              class="form__input"
              v-model="email"
              name="email"
              id="email"
              type="email"
              required
            />
          </div>
          <div class="form__box">
            <label class="form__label" for="password">Пароль</label>
            <input
              class="form__input"
              v-model="password"
              name="password"
              id="password"
              type="password"
              required
            />
          </div>
          <div class="form__box">
            <label class="form__label" for="file">Аватар</label>
            <input
              class="form__input"
              @change="handleFileUpload()"
              name="file"
              id="file"
              type="file"
              ref="file"
              accept="image/png, image/jpeg"
              required
            />
          </div>
          <div class="form__box">
            <button class="form__button" type="submit">Зарегистрироваться</button>
          </div>
        </form>
      </div>
    </section>
  </main>
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
Vue.use(VueAxios, axios);

export default {
  name: "RegistrationPage",
  props: {
    msg: String,
  },
  data() {
    return {
      email: "",
      password: "",
      username: "",
      file: "",
    };
  },
  methods: {
    handleFileUpload(){
        this.file = this.$refs.file.files[0];
    },
    RegistrationUser: function () {
      let api = "http://127.0.0.1:8000/api/users/register/";
      axios({
        // headers: {Authorization: `Bearer ${this.token}`},
        method: "post",
        url: api,
        data: {
          username: this.username,
          email: this.email,
          image: this.file,
          password: this.password,
        },
      }).then((response) => {
        console.log(response.data)
      });
      // },
    },
  },
};
</script>

<style scoped></style>
