<template>
    <main id="rating">
        <section class="rating">
            <div class="rating__wrapper">
                <h1 class="rating__title">Рейтинг игроков</h1>
                <ul class="rating__list">
                    <li v-for="user in users" :key="user.id" class="rating__item">
                        <img src="../../public/images/skyland01.jpeg" alt="Cover for location" class="rating__image">
                        <!-- <img :src="user.image" alt="Cover for location" class="rating__image"> -->
                        <div class="rating__info">
                            <h2 class="rating__name">{{ user.username }}</h2>
                            <p class="rating__scores"> {{ user.scores }}</p>
                            <p class="rating__levels">Прогресс: {{ user.levels }}'ый уровень</p>
                            <p class="rating__coins"> {{ user.money }} </p>
                        </div>
                    </li>
                </ul>
            </div>
        </section>
    </main>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)


export default {
  name: 'RatingPage',
  data(){
    return {
        users: [],
    }
  },
  methods: {
    GetUsers: function(){
        let api = 'http://127.0.0.1:8000/api/users/';
        Vue.axios.get(api).then((response) => {
            console.log(response.data);
            this.users = response.data;
        });
    }
  },
  mounted (){
    this.GetUsers();
  }
}
</script>

