<template>
  <main id="authorization">
    <section class="authorization">
      <div class="authorization__wrapper">
        <h1 class="authorization__title">Авторизация</h1>
        <form class="authorization__form form" @submit.prevent="LoginUser">
          <div class="form__box">
            <label class="form__label" for="email"
              >Адрес электронной почты</label
            >
            <input
              class="form__input"
              v-model="email"
              name="email"
              id="email"
              type="email"
              required
            />
          </div>
          <div class="form__box">
            <label class="form__label" for="password">Пароль</label>
            <input
              class="form__input"
              v-model="password"
              name="password"
              id="password"
              type="password"
              required
            />
          </div>
          <div class="form__box">
            <button class="form__button" type="submit">Войти в игру</button>
          </div>
        </form>
      </div>
    </section>
  </main>
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
Vue.use(VueAxios, axios);

export default {
  name: "AuthorizationPage",
  props: {
    msg: String,
  },
  data() {
    return {
      email: "",
      password: "",
    };
  },
  methods: {
    LoginUser: function () {
      let api = "http://127.0.0.1:8000/api/users/login/";
      axios({
        // headers: {Authorization: `Bearer ${this.token}`},
        method: "post",
        url: api,
        data: {
          email: this.email,
          password: this.password,
        },
      }).then((response) => {
        this.$emit("get-token", response.data.access);
      });
      // },
    },
  },
};
</script>

<style scoped></style>
