<template>
    <main id="products">
        <section class="levels">
            <div class="levels__wrapper">
                <h1 class="levels__title">Магазин "Все для злоключений"</h1>
                <ul class="levels__list">
                    <li v-for="product in products" :key="product.id" class="levels__item">
                        <img src="../../public/images/skyland01.jpeg" alt="Cover for location" class="levels__image">
                        <!-- <img :src="product.image" alt="Cover for location" class="levels__image"> -->
                        <div class="levels__info">
                            <h2 class="levels__name">{{ product.name }}</h2>
                            <p class="levels__description">
                                {{ product.description }}                       
                            </p>
                            <div class="levels__other">
                                <p class="levels__difficulty">{{ product.difficulty }}</p>
                                <p class="levels__coins"> {{ product.reward }} </p>
                                <p class="levels__best"> {{ product.best_user }} </p>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </section>
    </main>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'

Vue.use(VueAxios, axios)

export default {
  name: 'ShopPage',
  data(){
    return {
        products: [],
    }
  },
  methods: {
    GetProducts: function(){
        let api = 'http://127.0.0.1:8000/api/products/';
        Vue.axios.get(api).then((response) => {
            this.products = response.data;
        });
    }
  },
  mounted (){
    this.GetProducts();
  }
}
</script>

