<template>
  <main id="app">
    <router-view @get-token="getToken" @post-token="postToken"></router-view>
  </main>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      token: '',
    }
  },
  methods: {
    getToken (token){
      this.token = token;
    },
    postToken (){
      return this.token;
    }
  }
}
</script>
