<template>
    <main id="levels">
        <section class="levels">
            <div class="levels__wrapper">
                <h1 class="levels__title">Игровые уровни и локации</h1>
                <ul class="levels__list">
                    <li v-for="level in levels" :key="level.id" class="levels__item">
                        <img src="../../public/images/skyland01.jpeg" alt="Cover for location" class="levels__image">
                        <!-- <img :src="level.image" alt="Cover for location" class="levels__image"> -->
                        <div class="levels__info">
                            <h2 class="levels__name">{{ level.name }}</h2>
                            <p class="levels__description">
                                {{ level.description }}                       
                            </p>
                            <div class="levels__other">
                                <p class="levels__difficulty">{{ level.difficulty }}</p>
                                <p class="levels__coins"> {{ level.reward }} </p>
                                <p class="levels__best"> {{ level.best_user }} </p>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </section>
    </main>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)


export default {
  name: 'LevelsPage',
  props: {
    msg: String
  },
  data(){
    return {
        levels: [],
    }
  },
  methods: {
    GetLevels: function(){
        console.log(this.$emit('post-token'))
        let api = 'http://127.0.0.1:8000/api/levels/';
        // Vue.axios.get(api)
        axios ({
        headers: {Authorization: `Bearer ${this.$emit('post-token')}`},
        method: 'post',
        url: api,
        data: {
            email: this.email,
            password: this.password,
        }
      })
        .then((response) => {
            console.log(response.data);
            this.levels = response.data;
        });
    }
  },
  mounted (){
    this.GetLevels();
  }
}
</script>

